import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { FormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { DetailComponent } from './detail.component';
import { CartComponent } from './cart/cart.component';
import { NoteComponent } from './note/note.component';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component'; 
import { ScrollingModule } from '@angular/cdk/scrolling';
import { BillType14Module } from 'src/app/cafe/components/bill-type/bill-type-14/bill-type-14.module';
import { EditComponent } from './edit/edit.component';
const routes: Routes = [
  {
    path: "",
    component: DetailComponent
  },
  {
    path: "cart",
    component: CartComponent
  }, {
    path: "note",
    component: NoteComponent
  }, {
    path: "edit",
    component: EditComponent
  },
]

@NgModule({
  declarations: [DetailComponent, CartComponent, NoteComponent, EditComponent ], 
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    FormsModule,
    ScrollingModule,
    BillType14Module
  ],
  providers: [VhComponent]
})
export class DetailModule { }
